<template>
	<router-link :to="'/works/' + link" :class="[isNext ? $style.navLinkRight : $style.navLinkLeft, 'app-link']">
		<template v-if="isNext" >
			{{text}} &rsaquo;&rsaquo;
		</template>
		<template v-else :class="$style.navLinkLeft">
			&lsaquo;&lsaquo;{{text}}
		</template>
	</router-link>
</template>
<script>
export default {
	props: {
		link: { default: '' },
		isNext: { default: true },
		text: { default: null }
	}
}
</script>
<style lang="scss" module>
.navLinkLeft {
	text-align: left !important;
}

.navLinkRight {
	text-align: right !important;
}
</style>



