<template>
	<b-row :class="$style.navWork">
		<b-col cols="6" :class="$style.navLinkLeft">
				<app-nav-work-item v-if="prev" text="Prev Work" :link="prev" :isNext="false" />
		</b-col>
		<b-col cols="6" :class="$style.navLinkRight">
				<app-nav-work-item v-if="next" text="Next Work" :link="next" />
		</b-col>
	</b-row>
</template>
<script>
import NavWorkItem from '@/components/navigation/NavWorkItem.vue'
export default {
	props: {
		works: { default: [] },
		work: { default: '' }
	},
	computed: {
		prev () {
			return this.works[this.works.indexOf(this.work) - 1] || null
		},
		next () {
			return this.works[this.works.indexOf(this.work) + 1] || null
		}
	},
	components: {
		'app-nav-work-item': NavWorkItem
	}
}
</script>
<style lang="scss" module>
.navWork {
	padding:20px;
	font-size: 14px;
	height:100%;
}

.navLinkLeft {
	text-align: left !important;
}

.navLinkRight {
	text-align: right !important;
}

.navButtonLeft, .navButtonRight {
	display: flex;
	align-items: center;
	font-size: 26px;
	a {
		width:40px;
		height:40px;
		text-align:center;
	}
}

.navButtonLeft {
	justify-content: flex-start;
}

.navButtonRight {
	justify-content: flex-end;
}
</style>



