<template>
<div :class="$style.workContainer">
	<!-- <b-container fluid :class="[$style.backgroundImg, 'd-none d-md-flex']" :style="{backgroundImage: `url(${logo})`}"> -->
	<b-container fluid :class="[$style.backgroundImg, 'd-none d-md-flex']" v-lazy:background-image="logo">
	</b-container>
	<b-container :class="$style.content">
		<b-row :class="[$style.rowName, $style.row]" align-h="center">
			<b-col cols="11" md="12" lg="11" :class="$style.workName">
				<b-row align-h="center" :class="$style.workLink">
					<b-col cols="2">
						<app-nav-work-item v-if="prev" :link="prev" class="app-link d-none d-md-block" :isNext="false" />
					</b-col>
					<b-col cols="8">
						<a :href="work.link" target="_blank" class="app-link">{{work.name}}</a>
					</b-col>
					<b-col cols="2">
						<app-nav-work-item v-if="next" :link="next" class="app-link d-none d-md-block" />
					</b-col>
				</b-row>
			</b-col>
		</b-row>
		<b-row :class="[$style.rowInfo, $style.row]" align-h="center">
			<b-col cols="10" :class="[$style.colImg,'d-md-none']" >
				<img :src="logo" :class="$style.logo" />
			</b-col>
			<b-col cols="11" md="7" offset-md="1" order-md="2" :class="$style.colDescr">
				<div v-html="work.description"></div>
			</b-col>
			<b-col cols="11" md="4" offset-md="0" order-md="1">
				<h3 :class="$style.colTitle">techs used</h3>
				<app-grid-container :data="work.techs" colForRow="6" colForRowLg="4"/>
			</b-col>
		</b-row>
		<b-row align-h="center" :class="[$style.navigation, 'd-md-none']">
			<b-col cols="12">
				<app-nav-work :works="Object.keys(works)" :work="$route.params.work" type="mobile" />
			</b-col>
		</b-row>
	</b-container>
</div>
</template>
<script>
import works from '@/data/works.js'

import NavWorkItem from '@/components/navigation/NavWorkItem.vue'
import NavWork from '@/components/navigation/NavWork'
import GridContainer from '@/components/grids/GridContainer.vue'

export default {
	name: 'work',
	data () {
		return {
			prev: null,
			next: null,
			works: works,
			work: {},
			logo: ''
		}
	},
	beforeRouteUpdate (to, from, next) {
		if (!works[to.params.work]) {
			next({ path: '/notFound' })
		} else {
			this.initWork(this.$route.params.work)
			next()
		}
	},
	mounted () {
		this.initWork(this.$route.params.work)
	},
	methods: {
		initWork (w) {
			this.work = this.getWork(w)
			this.getImage()
			this.getNav()
		},
		getWork (work) {
			return works[work]
		},
		getImage () {
			this.imgLink().then(src => {
				this.logo = src
			})
		},
		getNav () {
			const keys = Object.keys(works)
			const index = keys.indexOf(this.$route.params.work)
			this.prev = keys[index - 1] || null
			this.next = keys[index + 1] || null
		}
	},
	computed: {
		imgLink () {
			return () => import(`@/assets/img/works/${this.$route.params.work}.png`)
		}
	},
	components: {
		'app-grid-container': GridContainer,
		'app-nav-work-item': NavWorkItem,
		'app-nav-work': NavWork
	}
}
</script>
<style lang="scss" module>

.workContainer {
	display: flex;
	flex-flow: column;
	flex:1;
}

.backgroundImg {
	min-height: 48vh;
	filter: grayscale(100%);
	background-size: cover;
	background-position: center center;
	// z-index:-1;
}

.backgroundImg[lazy=loading] {
	background-size: contain;
	background-repeat: no-repeat;
	height: auto;
}

.content {
	@include biggerThan($screen-md) {
		// transform: translateY(-60px);
		z-index: 10
	}
}

.navigation {
	flex:1;
	// padding-bottom: 20px;
}

.row {
	padding-bottom:20px;
	font-family: $font-text;

	@include biggerThan($screen-md) {
		// padding-bottom: 80px;
	}
}

.rowName {
	padding-top:20px;
	// text-decoration: underline;

	@include biggerThan($screen-md) {
		padding-top: 30px;
		transform:translateY(-60px)
	}
}

.rowInfo {
	@include biggerThan($screen-md) {
		// padding-top: 40px;
	}
}

.workName {
	text-align: center;
	font-size: $font-small;
	font-family: $font-text;
	letter-spacing: 3px;
}

.workLink {
	@include biggerThan($screen-md) {
		padding:15px 50px;
		background: $main-background;
		border-radius:8px;
	}
}

.colImg {
	text-align: center;
}

.logo {
	width: 100%;
	height: auto;
	filter: grayscale(100%);
	border-radius: 3px;

	@include lesserThan($screen-md) {
		margin-top: 20px;
	}
}

.colDescr,.colTech {
	padding-bottom: 20px;
}

.colDescr {
	text-align: justify;
	font-size: 16px;
	padding-top: 30px;
	line-height: 1.8;

	@include biggerThan($screen-md) {
		padding-top:0;
		padding-left:40px;
	}
}

.colTech {
	font-size: 16px;
	.singleTech {
		padding-top:10px;
		padding-bottom:10px;
		font-style: italic;
	}
	@include biggerThan($screen-md) {
		.singleTech {
			padding-top:20px;
		}
	}
}

.colTitle {
	text-align:center;
	font-family: $font-text;
	line-height: 1.6;
	text-transform: uppercase;

	@include biggerThan($screen-md) {
		text-align:left;
		// padding-left:10px;
	}
}
</style>



